body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-color: #6c757d;
  overflow-x: hidden;
}

.App {
  margin: auto;
}

.project-title{
  text-align: center;
  margin-top : 25px;
  color : white;
  font-size: 25px
}

.logo-move-area{
  margin: 25px auto;
  height: 500px;
  border : 15px solid #ffd400;
  width: 500px;
  background-color: white;
  border-radius: 10px
}

.control-moving-speed{
  position: relative;
  margin: auto;
  width: 100px;
  top : 50px;
}

.App-logo{
  position: relative;
  height: 50px;
  width: 50px;
}
